@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?ky5x4h');
  src:  url('../fonts/icomoon.eot?ky5x4h#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?ky5x4h') format('truetype'),
    url('../fonts/icomoon.woff?ky5x4h') format('woff'),
    url('../fonts/icomoon.svg?ky5x4h#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-ic-calendar:before {
  content: "\e901";
}
.icon-ic-credit-card:before {
  content: "\e905";
}
.icon-ic-face-black:before {
  content: "\e906";
}
.icon-ic-fingerprint:before {
  content: "\e907";
}
.icon-ic-mail:before {
  content: "\e908";
}
.icon-ic-phone:before {
  content: "\e909";
}

